.dayProductsWrapper {
  height: 241px;

  padding: 0 16px;
  border-radius: 12px;
  border: 1px solid rgba(239, 237, 232, 0.2);
  background: rgba(239, 237, 232, 0.05);
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25),
    0px 4px 4px 0px rgba(0, 0, 0, 0.25), 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}

.headerWrapper {
  display: flex;
  justify-content: space-between;
  padding: 12px;
  /* margin-bottom: 32px; */
}

.btnWrapper {
  display: flex;
  align-items: center;
  color: #e6533c;
}

.title {
  color: rgba(239, 237, 232, 0.5);
  font-size: 14px;
  line-height: 1.2;
}

.addProductBtn {
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;

  margin-right: 8px;
}

.errNotFound {
  color: rgba(239, 237, 232, 0.3);
  font-size: 16px;
  line-height: 24px;
  text-align: center;
}

@media screen and (min-width: 768px) {
  .dayProductsWrapper {
    width: 704px;
  }
}

@media screen and (min-width: 1440px) {
  .dayProductsWrapper {
    width: 826px;
  }
}
